<template>
  <div>
    <v-card>
      <v-card-text>
        <!-- <mrc_ess_staffMsg v-model="list.apply_staff_uuid"
                      :iconShow='true'
                      @changeList="changeList"></mrc_ess_staffMsg> -->
        <v-row>
          <v-col cols="6">
            <mrc_ess_multiText label="组群名称*"
                               class="mt-4"
                               :isNational="judegNational('remarks')"
                               v-model="list.remarks"
                               :readonly="disabled">
            </mrc_ess_multiText>
          </v-col>
          <v-col cols="3"
                 class="mt-3">
            <mrc_ess_NumInput v-model="number"
                              :readonly="disabled"></mrc_ess_NumInput>
          </v-col>
          <v-col cols="3"
                 class="mt-3">
          </v-col>
          <v-col cols="12">
            <mrc_ess_autoCompleteOrg v-model="list.org_uuid"
                                     :multiple='true'></mrc_ess_autoCompleteOrg>
          </v-col>
          <v-col cols="12">
            <h5 style="color:rgb(0, 0, 0, 0.38);font-size:20px;"
                class="mb-4 mt-10">组群成员</h5>
          </v-col>
          <v-col cols="12">
            <v-card class="mb-8"
                    style="border:1px solid #e0e0e0">
              <v-card-text>
                <v-col cols="12">
                  <mrc_ess_autoCompleteStaff v-model="list.staff_uuid"
                                             :multiple='true'>
                  </mrc_ess_autoCompleteStaff>
                </v-col>
                <v-col cols="12">
                  <mrc_ess_prompt :label="$t('label_department')+'*'"
                                  :errMsg="$errors('list','dept_uuid',requiredRule)"
                                  :rules='false'
                                  :multiple="true"
                                  :disabled='disabled'
                                  v-model="list.dept_uuid"
                                  :obj="dept_uuid">
                  </mrc_ess_prompt>
                </v-col>
                <v-col cols="12">
                  <!-- <mrc_ess_prompt :label="$t('label_Position_Title')+'*'"
                                  :errMsg="$errors('list','job_post_uuid',requiredRule)"
                                  :rules='false'
                                  :multiple="true"
                                  :disabled='disabled'
                                  v-model="list.job_post_uuid"
                                  :obj="job_post_uuid">
                  </mrc_ess_prompt> -->
                  <mrc_ess_autoCompletePosition v-model="list.job_post_uuid"
                                                :multiple="true"></mrc_ess_autoCompletePosition>
                </v-col>
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </v-card-text>
      <mrc_ess_submitBtn @success='success'
                         @cancel='cancel'
                         :loading='btnIsLoading'
                         v-if='!disabled'></mrc_ess_submitBtn>
    </v-card>

  </div>
</template>

<script> 
import {
  getStaffOfficialList
  , addStaffOfficial
  , updateStaffOfficial
  , deleteStaffOfficial
  , detailStaffOfficial
} from '@/service/api/staffmsg.js'
import { getBusinessList } from '@/service/api/prompt.js'
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
export default {
  components: {},
  props: {
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      number: 0,
      list: {},
      btnIsLoading: false,
      viewList: {},
    };
  },
  computed: {
    company_uuid () { //当前选中公司
      return this.$store.state.currentCompany
    },
    dept_uuid () {  //部门参照查询字段
      return { prompt_name: 'levelUuid_deptUuid', org_level_uuid: this.company_uuid }
    },
    job_post_uuid () {  //岗位查询
      // return { prompt_name: 'level-dept-post', dept_uuid: this.list.dept_uuid }
      return { prompt_name: 'level-dept-post' }
    },
    staff_probation_period () {
      return { prompt_name: 'staff_probation_period', org_level_uuid: this.company_uuid }
    }
  },
  created () { },
  watch: {
  },
  async mounted () {
    await this.national('mrbase_sys_staff_regular_employee')
    if (this.$store.state.uuid) {
      await this.getDetail()
      this.$route.meta.title = this.$store.state.subPage_name
    }
    else {

    }
  },

  methods: {
    paramsChange () { },
    changeList (val) {
      this.viewList = val
    },
    async getDetail () {
      const res = await detailStaffOfficial({ uuid: this.$store.state.uuid })
      if (res.code == 200) {
        this.list = res.data
        console.log(this.list);
      }
      else {

      }
    },
    async success () {
      this.$v.list.$touch()
      if (this.$v.list.$error) return
      if (this.$store.state.uuid) {
        updateStaffOfficial(this.$deletefield(null, this.list)).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
            this.getDetail()
          }
          else {
            this.$store.commit('newsTips', this.$t('message_The_modification_failed') + res.msg)
          }
        })
      }
      else {
        addStaffOfficial(this.list).then(res => {
          if (res.code == 200) {
            this.$store.commit('newsTips', this.$t('message_added_successfully'))
            this.$router.push({ name: 'staffLeave' })
          }
          else {
            this.$store.commit('newsTips', res.msg)
          }
        })
      }
    },
    cancel () {
      this.$myConfirm({
        content: '确认取消编辑'
      }).then(res => {
        this.$router.push({
          name: 'staffOfficial'
        })
      }).catch(err => { })
    },
  },
  validations: {
    list: {
      staff_uuid: {

      },
      dept_uuid: {

      },
      job_post_uuid: {

      }
    }
  },
};
</script>

